import '../scss/main.scss'

class Animal {
  constructor (name) {
    this.name = name
  }

  run () {
    console.log(`${this.name} run`)
  }
}

class Cat extends Animal {
  run () {
    super.run()
    console.log(`${this.name} now stop!`)
  }
}

class MyCat extends Cat {
  run () {
    super.run()
    console.log(`${this.name}`)
  }
}

const cat = new MyCat('vasya')

cat.run()
